import Vue from 'vue'
import router from './js/router.js'
import http from 'vue-resource'
import app from './app.vue'
import './app.scss'
import store from './js/vuex.js'
Vue.use(http)
new Vue({
	el: "#app",
	router,
	store,
	data: {
		list: []
	},
	components: {
		app
	},
	computed: {
		list() {
			return store.state.list
		}
	},
	created() {
		this.$http.get('mock/list.json').then((res) => {
				console.log(res.data.data)
				store.state.list = res.data.data

			},
			(res) => {
				console.log(res)
			})
	},
	template: '<app/>',
	render: h => h(app)
})